.App {
    width: 100vw;
    height: 100vh;
    display: flex;
    align-items: center;
    justify-content: center;
    flex-wrap: wrap;
}

.main-container {
    width: 300px;
    height: 400px;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: space-evenly;
    padding: 20px;
    box-shadow: -30px 0px 120px 30px rgba(197, 122, 140, 0.47); 
    border-radius: 30px;
}

.input-container {
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    gap:10px;
    width: 90%;
}

.input-container input {
    width: 90%;
    border: 2px solid black;
    padding: 10px;
    font-size: 16px;
}

.button-container {
    width: 100%;
    display: flex;
    justify-content: space-evenly;
}

.button-container button {
    background-color: #2B2B2B;
    width: 40px;
    height: 40px;
    color: white;
    border: none;
    display: flex;
    align-items: center;
    justify-content: center;
    border-radius: 5px;
    cursor: pointer;
    font-size: 16px;
}

h1 {
    color: #2B2B2B;
    font-size: 36px;
}

.error {
    color: red;
    font-size: 24px;
    font-weight: bold;
}

.success {
    color: green;
    font-size: 24px;
    font-weight: bold;
}

.result {
    font-size: 20px;
    font-weight: 500;
}